.pagination {
  justify-content: center;
  display: flex;
  padding-left: 0;
  list-style: none;
  overflow: hidden;
}

.page-link {
  display: block;
  border: 1px solid #00c1a3;
  border-radius: 5px;
  padding: 5px 10px;
  margin: 0 2px;
  text-decoration: none;
}

a.page-link:hover {
  background-color: #f3f6f8;
}
.page-item.active .page-link {
  color: #ffffff;
  background-color: #00c1a3;
}
.historyScreenLoadingAnimation {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 90vh;
}
